<template>
  <div>
    <ul>
      <router-link :to="'/detail/' + item.id" tag="li" class="item border-bottom" v-for="item of list" :key="item.id">
        <img class="item-img" :src="item.imgUrl" />
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">
            <span class="item-stars">
              <i class="iconfont item-star">&#xe642;</i>
              <i class="iconfont item-star">&#xe642;</i>
              <i class="iconfont item-star">&#xe642;</i>
              <i class="iconfont item-star">&#xe642;</i>
              <i class="iconfont item-star">&#xe642;</i>
            </span>
            {{item.comment}}条评论
          </p>
          <p class="item-price">
            <i class="iconfont item-price-icon">&#xe645;</i>
            {{item.price}}<span class="item-price-info">起</span>
          </p>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'SearchContent',
  data () {
    return {
      showList: []
    }
  },
  computed: {
    ...mapState({
      list: state => state.searchModule.list
    })
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/mixins.styl'
.title
  margin-top .2rem
  line-height .8rem
  background #eee
  text-indent .2rem
.item
  display flex
  height 1.9rem
  overflow hidden
  .item-img
    width 1.7rem
    height 1.7rem
    padding .1rem
  .item-info
    flex 1
    padding .1rem
    min-width 0
    .item-title
      line-height .54rem
      font-size .32rem
      ellipsis()
    .item-desc
      margin .06rem 0
      color #616161
      .item-stars
        font-size 0
        margin-right .16rem
        .item-star
          color #FFB436
          font-size .3rem
    .item-price
      margin-top .3rem
      color #ff8300
      font-size .4rem
      .item-price-icon
        font-size .14rem
        font-weight bold
        margin-right -.1rem
      .item-price-info
        font-size .14rem
        color #616161
</style>



