<template>
  <div class="header border-bottom">
    <router-link class="header-left" to="/" tag="div">
      <div class="iconfont back-icon">&#xe624;</div>
    </router-link>
    <input v-model="searchText" type="text" class="header-input" placeholder="景点/主题"/>
    <a class="header-btn" @click="getData">搜索</a>
  </div>
</template>

<script>
export default {
  name: 'SearchHeader',
  data () {
    return {
      searchText: ''
    }
  },
  computed: {
    searchContent () {
      return this.$store.state.searchModule.searchContent
    }
  },
  methods: {
    getData () {
      this.$store.dispatch('getSearchData')
      this.$store.commit('setSearchContent', this.searchText)
    }
  },
  activated () {
    this.searchText = this.$store.state.searchModule.searchContent
  }
}
</script>

<style lang='stylus' scoped>
.header
  margin .06rem 0
  display flex
  background #fff
  color #fff
  padding-bottom .08rem
  .header-left
    width .64rem
    float left
    .back-icon
      text-align center
      font-size .4rem
      color black
      height .76rem
      line-height .76rem
  .header-input
    flex 1
    height .64rem
    line-height .64rem
    margin-top .06rem
    margin-left .2rem
    padding-left .2rem
    background #F2F2F2
    border-radius .6rem
    color black
    text-align center
  .header-right
    min-width 1.04rem
    padding 0 .1rem
    float right
    text-align center
    color #fff
  .header-btn
    display: inline-block;
    width: .8rem;
    margin-right: .04rem;
    color: #212121;
    font-size: .28rem;
    text-align center
    line-height .76rem
</style>



